/*** Init Fonts ***/
@font-face {
    font-family: 'Kievit Pro';
    src: url('KievitPro-Regular.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
    font-feature-settings: "lnum" 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

