$bper-mobile: 767px;

.bper-primary-button {
    cursor: pointer;
    width: fit-content;
    background: transparent linear-gradient(
        180deg,
        #F06400 0%,
        #d04900 100%
    ) 0% 0% no-repeat padding-box;

    box-shadow: 0px 8px 16px #e3722214;
    text-shadow: 0px 3px 6px #071d1f3d;
    font-size: 16px;
    font-family: 'Kievit Pro Bold';
    height: 42px;
    border: none;
    border-radius: 24px;
    opacity: 1;
    transition: 0.3s background-color ease-out;
    color: #ffffff;
    padding: 0px 36px;
    line-height: 45px;
    margin: 0;

    &:hover {
        animation: hoverBtnPrimary 0.3s forwards;
    }

    &.disabled{
        width: fit-content;
        background: lightgray;
        box-shadow: 0px 8px 16px #e3722214;
        font-size: 16px;
        font-family: 'Kievit Pro Bold';
        height: 42px;
        border: none;
        border-radius: 24px;
        opacity: 1;
        color: #ffffff;
        padding: 0px 36px;
        line-height: 45px;
        cursor: default;
        text-shadow: none;

        &:hover{
            animation: none;
        }
    }
}

.bper-secondary-button{
    cursor: pointer;
    width: fit-content;
    border: 2px solid #e37222;
    color: #e37222;
    font-size: 16px;
    font-family: 'Kievit Pro Bold';
    height: 42px;
    border-radius: 24px;
    opacity: 1;
    transition: 0.3s background-color ease-out;
    padding: 0px 36px;
    line-height: 43px;
    margin: 0;

    &:hover {
        border: 2px solid #d04900;
        box-shadow: 0px 8px 16px #e3722214;
        background-color: white !important;
    }
}

.bper-text-button{
    cursor: pointer;
    width: -moz-fit-content !important;
    width: fit-content !important;
    background-color: transparent;
    font-family: 'Kievit Pro Bold';
    font-size: 16px;
    line-height: 22px;
    padding: 0 !important;
    transition: 3s background-color ease-out;
    border: none;
    color :#e37222;
    margin: 0;

    &:active {
        color: #d04900;
    }
}

.bper-text-button-tutorial{
    cursor: pointer;
    width: -moz-fit-content !important;
    width: fit-content !important;
    background-color: transparent;
    font-family: 'Kievit Pro Bold';
    //Figma and typography guidelines 
    font-size: 18px;
    //Modal guidelines
    //font-size: 16px;
    line-height: 22px;
    padding: 0 !important;
    transition: 3s background-color ease-out;
    border: none;
    color :#e37222;
    margin: 0;

    &:active {
        color: #d04900;
    }
}

@media screen and (max-width: $bper-mobile) {
    //Integration property to avoid first child of footer to change margin
    .bper-primary-button{
        margin-right: 0 !important;
    }
}

@keyframes hoverBtnPrimary {
    0% {
        background: transparent linear-gradient(180deg, #f06400 0%, #d04900 100%) 0% 0% no-repeat padding-box;
    }

    100% {
        background: transparent linear-gradient(180deg, #d04900 0%, #d04900 100%) 0% 0% no-repeat padding-box;
    }
}